import * as React from 'react';
import { useGetLoginInfo, useGetAccountInfo } from '@elrondnetwork/dapp-core';
import MintCard from './MintCard';
import TextCard from './TextCard';
import { useContext, useDispatch } from 'context';
import useContractValue from 'helpers/useContractValue';
import Login from '../../../components/Login/login';
import ConnectWalletCard from './ConnectWalletCard';
import { contractGetterNames } from 'my_constants';
import { Address, AddressValue } from '@elrondnetwork/erdjs/out';
import './card.scss';

interface OptionalClassnameProp {
    className?: string
}

const Card = ({ className = '' }: OptionalClassnameProp) => {

    const dispatch = useDispatch();
    const { showLogin } = useContext();
    const { address } = useGetAccountInfo();

    function handleHideLogin() {
        console.log('adios');
        dispatch( {type: 'hideLogin'});
    }

    const remainingNFT = useContractValue(contractGetterNames.remainingNFT, [], -1);
    const maxPerWallet = useContractValue(contractGetterNames.maxPerWallet, [], 10);
    const nftBalance = useContractValue(contractGetterNames.ownedNFT, [new AddressValue(new Address(address))]);

    const { isLoggedIn } = useGetLoginInfo();
    
    
    const TEXT_MAX_PER_WALLET_REACHED = <>
        Hey !<br />
        <br />
        You can‚Äôt mint more than {maxPerWallet} ninjas with this wallet.<br />
        <br />
        Thank you! ü•∑
    </>;

    const TEXT_SOLDOUT = <>
        Hey !<br />
        <br />
        All the ninjas are already sold out.<br />
        <br />
        Thank you for your support ! ü•∑
    </>;

const component = getCardComponent();
    return <div className={'card-container' + ' ' + className}>
        <div className='ap-card'></div>
        <div className='ap-card'></div>
        <div className='ap-card'>
            {component}
        </div >
        <Login show={showLogin || false} onHide={handleHideLogin} />
    </div>;

    

    function getCardComponent(): JSX.Element {

        switch (new URLSearchParams(window.location.search).get('debug_card')?.trim()) {
            case 'mint': return <MintCard />;
            case 'connect': return <ConnectWalletCard />;
            case 'soldout': return <TextCard paragraph={TEXT_SOLDOUT} />;
            case 'max': return <TextCard paragraph={TEXT_MAX_PER_WALLET_REACHED} />;
        }

        const isSoldout = remainingNFT == 0;
        const maxPerWalletReached = nftBalance >= maxPerWallet;

        if (isLoggedIn == false) {
            return <ConnectWalletCard />;
        }
        else if (isSoldout == true) {
            return <TextCard paragraph={TEXT_SOLDOUT} />;
        }
        else if (maxPerWalletReached == true) {
            return <TextCard paragraph={TEXT_MAX_PER_WALLET_REACHED} />;
        }
        else {
            return <MintCard />;
        }
    }
};

export default Card;


