import { Address, AddressValue, Balance } from '@elrondnetwork/erdjs/out';
import { contractGetterNames } from 'my_constants';
import * as React from 'react';
import MintCardFooter from '../Footer';
import {
    transactionServices,
    useGetAccountInfo,
    useGetPendingTransactions,
    refreshAccount,
    useGetNetworkConfig
  } from '@elrondnetwork/dapp-core';
import useContractValue from 'helpers/useContractValue';
import InputSteppers from './InputSteppers';
import './MintCard.scss';
import MintWarning from './MintWarning';
import { contractAddress } from 'config';

const MintCard = () => {
    const { address } = useGetAccountInfo();
    const [nftInCart, setNftInCart] = React.useState(1);
    const nftPrice = useContractValue(contractGetterNames.nftPrice, [], -1);
    const ownedNft = useContractValue(contractGetterNames.ownedNFT, [new AddressValue(new Address(address))]);
    const maxPerWallet = useContractValue(contractGetterNames.maxPerWallet, [], 2);


    const sendMintTransaction = async (nftInCart:number) => {
        const { sendTransactions } = transactionServices;
        const totalMintAmount = (nftInCart.toString(16).length < 2 ? '0' : '') +nftInCart.toString(16);
    
        const minTx = {
          value: (nftPrice * nftInCart).toString(),
          data: 'mint@' + totalMintAmount,
          receiver: contractAddress
        };
        await refreshAccount();
    
        const { sessionId /*, error*/ } = await sendTransactions({
          transactions: minTx,
          transactionsDisplayInfo: {
            processingMessage: 'Processing Mint transaction',
            errorMessage: 'An error has occured during Minting',
            successMessage: 'Mint transaction successful'
          },
          redirectAfterSign: false
        });
        /*if (sessionId != null) {
          setTransactionSessionId(sessionId);
        }*/
      };

    return <div className='mint-card'>
        {(ownedNft + nftInCart > maxPerWallet) &&
            <MintWarning header='Warning' paragraph={<p>Sorry. You can&apos;t mint more at this time.<br />The limit is set to {maxPerWallet} ü•∑ per wallet.</p>} />
        }

        <h1 className='text-center'>ùï∏ùñéùñìùñô ùñûùñîùñöùñó ùñìùñéùñìùñèùñÜ&apos;ùñò!</h1>

        <InputSteppers defaultValue={1} minValue={1} maxValue={maxPerWallet - ownedNft} onChange={setNftInCart} />

        <button className='mt-3 mb-2 w-100' onClick={(e) => sendMintTransaction(nftInCart)}>Mint</button>

        <div className='d-flex justify-content-between'>
            <p>{nftPrice > 0 ? nftPrice / 10 ** 18 : '-'} EGLD per ü•∑</p>
            <p><span className='discret-text'>Total</span> {nftPrice > 0 ? nftPrice * nftInCart / 10 ** 18 : '-'} EGLD</p>
        </div>

        <MintCardFooter />
    </div >;
};

export default MintCard;