import * as React from 'react';
import './InputSteppers.scss';


interface InputSteppersProps {
    defaultValue: number,
    minValue: number,
    maxValue: number,
    onChange?: (value: number) => void
}

const InputSteppers: React.FC<InputSteppersProps> = ({
    defaultValue = 1,
    minValue = Number.NEGATIVE_INFINITY,
    maxValue = Number.POSITIVE_INFINITY,
    onChange = undefined
}: InputSteppersProps) => {

    const clamp = (num: number, min: number, max: number) => Math.min(Math.max(num, min), max);
    const [value, _setValue] = React.useState(defaultValue);

    const setValue = (newValue: number) => {

        newValue = clamp(newValue, minValue, maxValue);
        _setValue(newValue);

        if (onChange != undefined) {
            onChange(newValue);
        }
    };

    const increment = () => setValue(value + 1);
    const decrement = () => setValue(value - 1);

    const plusButtonFill = value == maxValue ? '#B8B8B8' : '#fff667';
    const minusButtonFill = value == minValue ? '#B8B8B8' : '#fff667';

    const plusButtonClass = value == maxValue ? 'disabled' : '';
    const minusButtonClass = value == minValue ? 'disabled' : '';

    return <div className='input-steppers d-flex justify-content-center  align-items-center'>

        <svg className={minusButtonClass} width='39' height='39' viewBox='0 0 39 39' fill='none' onClick={decrement} xmlns='http://www.w3.org/2000/svg'>
            <circle cx='19.5' cy='19.5' r='18.5' stroke={minusButtonFill} strokeWidth='2' />
            <rect x='11' y='19' width='17' height='2' fill={minusButtonFill} />
        </svg>

        <p className='mx-3'>{value}</p>

        <svg className={plusButtonClass} width='39' height='39' viewBox='0 0 39 39' onClick={increment} fill='none' xmlns='http://www.w3.org/2000/svg'>
            <circle cx='19.5' cy='19.5' r='18.5' stroke={plusButtonFill} strokeWidth='2' />
            <rect x='11' y='19' width='17' height='2' fill={plusButtonFill} />
            <rect x='19' y='28' width='17' height='2' transform='rotate(-90 19 28)' fill={plusButtonFill} />
        </svg>
    </div>;
};

export default InputSteppers;