import React, { useState, useEffect } from 'react';
import { DappUI, useGetLoginInfo } from '@elrondnetwork/dapp-core';
import { Modal } from 'react-bootstrap';
import { routeNames } from '../../routes';
//import './styles.scss';

interface LoginMethodType {
  icon?: any;
  label?: string;
  onSelect?: any;
  link?: string;
  className?: string;
  style?: any;
  comment?: string;
}

const LoginMethod = ({
  icon,
  label,
  onSelect,
  link,
  className,
  style,
  comment
}: LoginMethodType) => {
  function handleOnSelect(e: React.MouseEvent) {
    e.preventDefault();
    if (!!onSelect) {
      onSelect(e);
    }
  }

  const currentClassName =
    'login-method container' + (className ? ' ' + className : '');

  const body = <div className = 'row d-flex align-items-center'>
                    <div className='col-2'>
                        {icon}
                    </div>
                    <div className='col text-start'>
                        {label}
                        {comment && <p className='text-muted' style={{fontSize: '11px'}}><sup>*</sup>{comment}</p>}
                    </div>
                    <div className='col-2 text-end'>
                        {/* <span><ArrowRightIcon /></span> */}
                    </div>
                </div>;

    return (
        <div className={currentClassName} style={style}>
            {link? 
            <a href={link||'#'} style={{textDecoration:'none'}}>
                {body}            
            </a>
            : 
            <a href='#' onClick={handleOnSelect} style={{textDecoration:'none'}}>
                {body}            
            </a>
            }
        </div>
    );
};

function isMaiarExtensionInstalled(): boolean {
    return (!!window && !!window.elrondWallet);
}

interface LoginType {
    show: boolean;
    onHide?: any;
}

const Login = ({show, onHide}: LoginType)=> {
    const callbackRoute = '/';
    const {
        ExtensionLoginButton,
        WebWalletLoginButton,
        LedgerLoginButton,
        WalletConnectLoginButton
      } = DappUI;

    const { isLoggedIn } = useGetLoginInfo();
    if(isLoggedIn){
        show = false;
    }
    
    const [maiarExtension, setMaiarExtension] = useState<boolean>(false);
    const ref = React.useRef(null);


    useEffect(() => {
        setMaiarExtension(isMaiarExtensionInstalled());
    }, []);

    const classNameMaiarExtension = ['border-bottom'];
    if (maiarExtension) {
        classNameMaiarExtension.push('pb-3 pt-3');
    } else {
        classNameMaiarExtension.push('pb-1 pt-2');
    }





    return (
        <Modal ref={ref} show={show} onHide={onHide} className='login-qo-w' aria-labelledby='contained-modal-title-vcenter' centered>
            <Modal.Header closeButton />
            <Modal.Body className='text-center'>
                {/* <EnterIcon className='mb-3'/> */}
                <Modal.Title>Please select your login method</Modal.Title>
                <div className='container ps-5 pe-5 pt-4 pb-4'>
                    {/* <!--LoginMethod link={routeNames.walletconnect} icon={MaiarAppIcon} label='Maiar App' className='pb-3 border-bottom'/>
                    <LoginMethod onSelect={extensionLogin} icon={MaiarExtensionIcon} label='Maiar Extension' className={classNameMaiarExtension.join(' ')} comment={!maiarExtension?t('installMaiarExtension'): undefined}/>
                    <LoginMethod link={routeNames.ledger} icon={LedgerIcon} label='Ledger' className='pb-3 pt-3 border-bottom'/>
                    <LoginMethod onSelect={webWalletLogin} icon={ElrondWebWalletIcon} label='Web Wallet' className='pb-3 pt-3/> */}
                    <ExtensionLoginButton
                    callbackRoute={routeNames.home}
                    loginButtonText={'Extension'}
                    />
                    <WebWalletLoginButton
                    callbackRoute={routeNames.dashboard}
                    loginButtonText={'Web wallet'}
                    />
                    <LedgerLoginButton
                    loginButtonText={'Ledger'}
                    callbackRoute={routeNames.dashboard}
                    className={'test-class_name'}
                    />
                    <WalletConnectLoginButton
                    callbackRoute={routeNames.dashboard}
                    loginButtonText={'Maiar'}
                    />
                </div>
            </Modal.Body>
        </Modal>
    );
};

export default Login;