import * as React from 'react';
import './MintWarning.scss';

interface WarningProps {
    header: string,
    paragraph: string | JSX.Element
}

const MintWarning: React.FC<WarningProps> = ({ header, paragraph }: WarningProps) => {
    return <div className='mint-warning'>
        <h2>{header}</h2>
        <p>{paragraph}</p>
    </div>;
};

export default MintWarning;