import React from 'react';
import { useState, useEffect } from 'react';
import { useGetNetworkConfig } from '@elrondnetwork/dapp-core';
import {
    Address,
    AddressValue,
    ContractFunction,
    ProxyProvider,
    Query,
} from '@elrondnetwork/erdjs';

import { contractAddress } from 'config';

function useContractValue(functionName: string, args: any = [], defaultValue = 0) {
    const [value, setValue] = React.useState<number>(defaultValue);
    const { network } = useGetNetworkConfig();

    useEffect(() => {
        const query = new Query({
            address: new Address(contractAddress),
            func: new ContractFunction(functionName),
            args: args,
        });
        const proxy = new ProxyProvider(network.apiAddress);
        proxy
            .queryContract(query)
            .then(({ returnData }) => {
                const [encoded] = returnData;

                if (encoded == undefined || encoded == '') return;

                const decoded = Buffer.from(encoded, 'base64').toString('hex');
                const parsed = parseInt(decoded, 16);

                if (isNaN(parsed) == false) {
                    setValue(parsed);
                }
                else {
                    //console.error('Failed to parse output from function', functionName, '. Raw output '' + encoded + ''. Args was '', args, ''.');
                }
            })
            .catch((err) => {
                console.error('Unable to call VM query', err);
            });
    });

    return value;
}

export default useContractValue;