import { Address, AddressValue } from '@elrondnetwork/erdjs/out';
import { useGetAccountInfo } from '@elrondnetwork/dapp-core';
import useContractValue from 'helpers/useContractValue';
import { contractGetterNames } from 'my_constants';
import * as React from 'react';
import './MintCardFooter.scss';


const MintCardFooter = (props: any) => {
    const { address } = useGetAccountInfo();
    const ownedNFT = useContractValue(contractGetterNames.ownedNFT, [new AddressValue(new Address(address))], 0);
    const remainingNFTAmount = useContractValue(contractGetterNames.remainingNFT, [], -1);
    const totalNFT = useContractValue(contractGetterNames.totalAmount, [], -1);


    return <div className={'mint-card-footer w-100' + ' ' + (props.className || '')}>
        <p className='text-center'>
            <span className='discret-text'>your balance</span> {ownedNFT > 0 ? ownedNFT : '-'} ü•∑<br />
            {totalNFT > 1 ? ((totalNFT - remainingNFTAmount)) : '-'} / {totalNFT > 0 ? (totalNFT) : '-'} ü•∑
        </p>
    </div>;
};

export default MintCardFooter;